input {
    background-color: white;
}

select {
    background-color: white;
}


body {
    background-image: url(../../assets/images/86678400157f31c53be1.webp);
    margin:0;
    padding: 0;
}

h1 {
    margin: 0;
    padding: 0;
}

.formItem {
    display: flex;
    flex-direction: column;
}

.formWrapper {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
}

form {
    background-color: rgba(0,0,0,.3);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem 3rem;
    border-radius: 4px;
    gap: 1rem;
    color: white;
}

#loginErrorMsg {
    color: red;
    background-color: rgba(255,255,255,.4);
    font-size: 1.2rem;
    padding: .125rem .25rem;
    text-align: center;
}
